<script>
export default {
  name: "App",
  components: {},

  data() {
    return {
      scrollBarWidth: 0,
      openTL: null,
      joinusTL: null,
      homeAnmiTL: null,
      aboutAnmiTL: null,
      partnerAnmi: null,
    };
  },
  methods: {
    getScrollbarWidth() {
      const outer = document.createElement("div");
      outer.style.visibility = "hidden";
      outer.style.width = "100px";
      outer.style.position = "absolute";
      outer.style.top = "-9999px";
      document.body.appendChild(outer);
      const widthNoScroll = outer.offsetWidth;
      outer.style.overflow = "scroll";
      const inner = document.createElement("div");
      inner.style.width = "100%";
      outer.appendChild(inner);
      const widthWithScroll = inner.offsetWidth;
      outer.parentNode.removeChild(outer);
      this.scrollBarWidth = widthNoScroll - widthWithScroll;
    },
    adjustBanner() {
      this.gsap.set(".banner", {
        right: this.scrollBarWidth,
      });
    },
    openAnmi() {
      this.openTL = this.gsap.timeline({ onComplete: this.homeAnmi });
      this.openTL
        .fromTo(
          [".verBar"],
          {
            height: 0,
            // scaleY: 0,
          },
          {
            height: "auto",
            delay: 0.5,
            duration: 0.5,
            // scaleY: 1,
            transformOrigin: "top top",
            ease: "power2.out",
          }
        )
        .to([".logoText-1", ".logoText-2"], {
          left: "2%",
          duration: 1,
          stagger: 0.1,
          ease: "power3.out",
        })
        .to(".loader", {
          autoAlpha: 0,
        });
    },
    initBannerAnmi() {
      this.ScrollTrigger.create({
        scroller: ".container",
        trigger: ".about",
        start: "top 40%",
        end: 99999,
        // markers: true,
        toggleClass: {
          targets: ".banner-main",
          className: "banner-main-scrolled",
        },
        duration: 1,
      });
      this.ScrollTrigger.create({
        scroller: ".container",
        trigger: ".about",
        start: "top 40%",
        end: 99999,
        toggleClass: {
          targets: ".banner-cat-item",
          className: "banner-cat-item-scrolled",
        },
        duration: 1,
      });
    },

    homeAnmi() {
      this.homeAnmiTL = this.gsap.timeline();
      this.homeAnmiTL
        .to(".home-main-context-bar", {
          height: "auto",
          duration: 1,
        })
        .to(
          ".bottom-bar",
          {
            width: "auto",
            duration: 1,
          },
          "<"
        )
        .to(
          ".text-head",
          {
            autoAlpha: 1,
            left: 0,
            duration: 1,
          },
          "-=0.5"
        )
        .to(
          ".home-main-bio-row div",
          {
            top: 0,
            duration: 1,
            autoAlpha: 1,
          },
          "<"
        )
        .to(".text-tail", {
          autoAlpha: 1,
          duration: 1,
        });
    },

    imgRataion() {
      this.gsap.to(".home-main-funder-avator", {
        rotation: "330",
        repeat: -1,
        duration: 60,
        ease: "none",
      });
    },
    initAboutAnmi() {
      this.aboutAnmiTL = this.gsap.timeline({
        scrollTrigger: {
          scroller: ".container",
          trigger: ".about-main-bar",
        },
      });
      this.aboutAnmiTL
        .to(".about-main-bar", {
          duration: 0.5,
          width: "auto",
        })
        .from([".about-main-title", ".about-main-2 div"], {
          top: "-10%",
          autoAlpha: 0,
          duration: 0.5,
        })
        .from(".about-main-core", {
          top: "-10%",
          autoAlpha: 0,
          duration: 0.5,
        })
        .to(".about-main-coreImg div", {
          width: "auto",
          duration: 0.5,
          stagger: 0.1,
        });
    },
    initPartnerAnmi() {},
  },
  created() {
    this.getScrollbarWidth();
    console.log(this.scrollBarWidth);
  },
  mounted() {
    this.openAnmi();
    this.adjustBanner();
    this.initBannerAnmi();
    // this.homeAnmi();
    this.imgRataion();
    this.initAboutAnmi();
    this.initPartnerAnmi();
  },
};
</script>

